import { createBrowserHistory } from 'history';
import {
  connectRouter,
  routerMiddleware as createRouterMiddleware,
} from 'connected-react-router';

const isAuth = () => {
  const authEnv = process.env.IS_AUTH;
  if (authEnv) return JSON.parse(authEnv);
  return false;
};

const getAuthBaseName = () => window.location.pathname;

const getAppBaseName = () => {
  const basenameSplitted = window.location.pathname.split('/');
  return (
    basenameSplitted.splice(0, basenameSplitted.length - 1).join('/') + '/'
  );
};

const history = createBrowserHistory({
  basename: isAuth() ? getAuthBaseName() : getAppBaseName(),
});

const routerReducer = connectRouter(history);
const routerMiddleware = createRouterMiddleware(history);

function scrollToTop() {
  window.scrollTo(0, 0);
}
history.listen(scrollToTop);

export { history, routerReducer, routerMiddleware };
