import React, { useCallback } from 'react';
import { get } from 'lodash';
import AuthForm from 'components/AuthForm/AuthForm';
import Button from 'components/Button/Button';
import './Grant.scss';
import { object, func } from 'prop-types';
import { withTranslation } from 'react-i18next';
import { SIGN_IN } from 'modules/routes';

const Grant = ({ params, t, redirectTo, grantAcceptFlow }) => {
  const requestedPermissions = get(params, 'requestedPermissions');
  if (!requestedPermissions) return null;

  const parsedRP = JSON.parse(requestedPermissions);
  const DescrioptionGrant = () => {
    return (
      <div>
        <span>{t('descriptionGrantText')}</span>
        <ul>
          {parsedRP.map((val, key) => (
            <li key={key}>
              {[t('accessTo'), t(val.level), t('your'), val.defaultName].join(
                ' '
              )}
            </li>
          ))}
        </ul>
      </div>
    );
  };
  const handlerAccept = useCallback(() => {
    grantAcceptFlow();
  }, [grantAcceptFlow]);
  const handlerReject = useCallback(() => {
    redirectTo(SIGN_IN);
  }, [redirectTo]);
  return (
    <AuthForm
      withIcon={false}
      title={t('grantTitle')}
      description={<DescrioptionGrant />}
    >
      <div className="button-block row">
        <Button className="btn-danger" text="reject" onClick={handlerReject} />
        <Button className="btn-primary" text="accept" onClick={handlerAccept} />
      </div>
    </AuthForm>
  );
};

Grant.propTypes = {
  grantAcceptFlow: func.isRequired,
  params: object.isRequired,
  t: func.isRequired,
  redirectTo: object.isRequired,
};

export default withTranslation()(Grant);
