import React from 'react';
import { bool, string, func } from 'prop-types';
import './Button.scss';
import { Button as ButtonBP } from '@blueprintjs/core';
import { useTranslation } from 'react-i18next';
import clsx from 'clsx';
import './Button.scss';

const Button = ({
  upperCase,
  loading,
  onClick,
  text,
  large,
  className,
  ...props
}) => {
  const { t } = useTranslation();
  return (
    <ButtonBP
      {...props}
      large={large}
      loading={loading}
      minimal={true}
      className={clsx('btn', className, { upper_case: upperCase })}
      active={false}
      text={t(text)}
      onClick={onClick}
    />
  );
};
Button.propTypes = {
  ...ButtonBP.propTypes,
  upperCase: bool,
  loading: bool,
  className: string,
  onClick: func.isRequired,
  text: string,
  large: bool,
};
Button.defaultProps = {
  ...ButtonBP.defaultProps,
  upperCase: false,
  large: true,
  text: '',
  className: '',
  loading: false,
};
export default Button;
