import React from 'react';
import { connect } from 'react-redux';
import { node, string } from 'prop-types';
import { useTranslation } from 'react-i18next';
import { getAllParams } from 'utils/query';
import { ACCESSED_LANGUAGES } from '../../../../i18n';
import { setLanguage } from 'utils/ls';

const Wrapper = ({ children, language }) => {
  const { i18n } = useTranslation();
  if (ACCESSED_LANGUAGES.includes(language)) {
    i18n.changeLanguage(language);
    setLanguage(language);
  }
  return <div className="wrapper">{children}</div>;
};
Wrapper.propTypes = {
  children: node.isRequired,
  language: string,
};
Wrapper.defaultProps = {
  children: null,
  language: 'en',
};

const connector = connect(
  ({ router: { location } }) => {
    const { lang } = getAllParams(location);
    return { language: lang };
  },
  () => ({})
);
export default connector(Wrapper);
