import { takeLatest, put, select, call } from 'redux-saga/effects';
import { types, actions } from 'modules/auth/reducers/auth';
import { push } from 'react-router-redux';
import { get } from 'lodash';
import API from '../../../utils/api';
import { checkCredentialsOrRedirect } from 'utils/auth';
import { setAccessToken, setUsername, getUsername } from 'utils/ls';
import { generateUsername } from 'utils/generateUsername';
import validators from 'utils/validators';
import { setLoginToken } from '../../../utils/ls';
import { GRANT } from '../../routes';
import { getAllParams } from 'utils/query';
import { getCorrectFlow } from 'utils/links';
const APP_ID = 'riva=obmp';

function* loginFlow(data) {
  yield put(actions.setLoginFlow());
  yield* login(data);
  yield* checkApp(data);
  yield* pollAccessConfirm();
}

function* grantAcceptFlow() {
  yield* giveAccesses();
  yield* pollAccessConfirm();
}

function* registerFlow(data) {
  yield put(actions.setRegisterFlow());
  yield* register(data);
  yield* login(data);
  yield* checkApp(data);
}

function* checkUID({ payload }) {
  const { email } = payload;
  try {
    const data = yield API.get(`https://reg.obpmprod.ch/${email}/uid`);
    if (data) {
      yield put(actions.checkUid.success(data));
    }
  } catch (e) {
    yield put(actions.checkUid.failure());
  }
}

function* checkUsername() {
  const username = generateUsername();

  try {
    const data = yield API.get(
      `https://reg.obpmprod.ch/${username}/check_username`
    );
    if (data) {
      yield put(actions.checkUsername.success({ ...data, username }));
      setUsername(username);
    }
  } catch (e) {
    yield put(actions.checkUsername.failure());
  }
}

function* pollAccessConfirm({ accessToken } = {}) {
  const { location } = yield select(state => state.router);
  const { isLoginFlow, isRegisterFlow } = yield select(state => state.auth);
  const { key, lang } = getAllParams(location);

  const {
    usernameData: { username = getUsername() },
    accessData: {
      access: { token = accessToken },
    },
  } = yield select(state => state.auth);

  const tokenDataModel = {
    redirectTo: getCorrectFlow({ isLoginFlow, isRegisterFlow }),
    language: lang,
    accessToken: token,
  };

  const body = {
    status: 'ACCEPTED',
    username,
    token: JSON.stringify(tokenDataModel),
    code: 200,
  };

  try {
    const data = yield API.post(`https://reg.obpmprod.ch/access/${key}`, {
      body,
    });
    if (data) {
      yield put(actions.pollAccessConfirm.success(data));
    }
  } catch (e) {
    yield put(actions.pollAccessConfirm.failure(e));
  }
}

function* pollAccess() {
  const { location } = yield select(state => state.router);
  const { key } = getAllParams(location);

  try {
    const data = yield API.get(`https://reg.obpmprod.ch/access/${key}`);
    const accessToken = get(data, 'accesses[0].token');
    if (accessToken) {
      setAccessToken(accessToken);
    }

    if (data) {
      yield put(actions.pollAccess.success(data));
    }
  } catch (e) {
    yield put(actions.pollAccess.failure(e));
  }
}

function* accessInfo() {
  checkCredentialsOrRedirect();
  const { username, loginToken } = yield select(state => state.loginData);

  try {
    const data = yield API.get(
      `https://${username}.obpmprod.ch/access-info?auth=${loginToken}`
    );

    if (data) {
      yield put(actions.accessInfo.success(data));
    }
  } catch (e) {
    yield put(actions.accessInfo.failure(e));
  }
}

function* giveAccesses() {
  const {
    loginData: { token },
    usernameData: { username },
    checkAppData: { checkedPermissions },
  } = yield select(state => state.auth);

  const { location } = yield select(state => state.router);
  const { requestingAppId } = getAllParams(location);
  const body = {
    type: 'app',
    name: requestingAppId,
    permissions: checkedPermissions,
  };

  try {
    const data = yield API.post(`https://${username}.obpmprod.ch/accesses`, {
      body,
      headers: {
        Authorization: token,
      },
    });
    const accessToken = get(data, 'access.token');
    if (accessToken) {
      setAccessToken(accessToken);
    }

    if (data) {
      yield put(actions.giveAccess.success(data));
    }
  } catch (e) {
    yield put(actions.giveAccess.failure(e));
  }
}

function* checkApp({ payload }) {
  const { username } = payload;
  checkCredentialsOrRedirect();
  const {
    loginData: { token },
  } = yield select(state => state.auth);

  const { location } = yield select(state => state.router);
  const { requestingAppId, requestedPermissions } = getAllParams(location);
  const body = {
    requestingAppId,
    requestedPermissions: JSON.parse(requestedPermissions),
  };

  try {
    const data = yield API.post(
      `https://${username}.obpmprod.ch/accesses/check-app`,
      {
        body,
        headers: {
          Authorization: token,
        },
      }
    );
    const matchingAccessToken = get(data, 'matchingAccess.token');
    if (matchingAccessToken) {
      yield* pollAccessConfirm({ accessToken: matchingAccessToken });
    }
    if (data) {
      yield put(actions.checkApp.success(data));
    }
  } catch (e) {
    yield put(actions.checkApp.failure(e));
  }
}

function* login({ payload }) {
  const { password, username: usernameValue } = payload;

  const isEmail = validators.email(usernameValue);

  let username = usernameValue;
  let body = { password, appId: APP_ID, username };

  if (isEmail) {
    const email = username;
    yield* checkUID({ payload: { email } });
    const {
      uidData: { uid },
    } = yield select(state => state.auth);
    body = { ...body, username: uid };
  }

  setUsername(body.username);

  try {
    const data = yield API.post(`https://${username}.obpmprod.ch/auth/login`, {
      body,
    });
    if (data) {
      setLoginToken(data.token);
      yield put(actions.login.success(data));
    }
  } catch (e) {
    yield put(actions.login.failure(e));
  }
}

function* redirect({ payload }) {
  const { to } = payload;
  const { location } = yield select(state => state.router);
  const concattedLink = to + location.search;
  yield put(push(concattedLink));
}

function* register({ payload }) {
  const { password, username, email } = payload;

  let body = {
    languageCode: 'en',
    hosting: 'exoscale.ch-ch1',
    invitationtoken: 'enjoy',
    appid: 'riva=obmp',
    password,
    username,
    email,
  };
  try {
    const data = yield API.post('https://reg.obpmprod.ch/user', {
      body,
    });
    if (data) {
      yield put(actions.register.success(data));
      yield call(redirect, { payload: { to: GRANT } });
    }
  } catch (e) {
    yield put(actions.register.failure(e));
  }
}

export default function* sagas() {
  yield takeLatest(types.REGISTER.REQUEST, registerFlow);
  yield takeLatest(types.LOGIN.REQUEST, login);
  yield takeLatest(types.CHECK_APP.REQUEST, checkApp);
  yield takeLatest(types.ACCESS_INFO.REQUEST, accessInfo);
  yield takeLatest(types.CHECK_USERNAME.REQUEST, checkUsername);
  yield takeLatest(types.CHECK_UID.REQUEST, checkUID);
  yield takeLatest(types.GIVE_ACCESSES.REQUEST, giveAccesses);
  yield takeLatest(types.REDIRECT_TO, redirect);
  yield takeLatest(types.GRANT_ACCEPT_FLOW, grantAcceptFlow);
  yield takeLatest(types.LOGIN_FLOW, loginFlow);
  yield takeLatest(types.POLL_ACCESS.REQUEST, pollAccess);
  yield takeLatest(types.POLL_ACCESS_CONFIRM.REQUEST, pollAccessConfirm);
}
