import {
  createRequestAction,
  createRequestTypes,
  createReducer,
  createRequestHandlers,
  createAction,
} from 'helpers/reduxHelpers';
import { getUsername, getLoginToken } from 'utils/ls';

export const NAME = 'auth';

const initialState = {
  isAuthorized: false,
  fetching: false,
  checkAppData: {},
  uidData: null,
  accessData: {
    access: {},
  },
  usernameData: {
    reserved: false,
    username: getUsername(),
  },
  accessInfo: {
    type: '',
    name: '',
  },
  loginData: {
    preferredLanguage: '',
    token: getLoginToken(),
  },
  registerData: {
    token: '',
    preferredLanguage: 'en',
  },
  pollAccess: {},
  pollAccessConfirm: {},
  error: null,
  showDownloadApp: true,
  isLoginFlow: false,
  isRegisterFlow: false,
};

export const types = {
  LOGIN: createRequestTypes(`${NAME}_LOGIN`),
  LOGIN_FLOW: `${NAME}_LOGIN_FLOW`,
  REGISTER: createRequestTypes(`${NAME}_REGISTER`),
  CHECK_APP: createRequestTypes(`${NAME}_CHECK_APP`),
  ACCESS_INFO: createRequestTypes(`${NAME}_ACCESS_INFO`),
  CHECK_USERNAME: createRequestTypes(`${NAME}_CHECK_USERNAME`),
  CHECK_UID: createRequestTypes(`${NAME}_CHECK_UID`),
  GIVE_ACCESSES: createRequestTypes(`${NAME}_GIVE_ACCESSES`),
  POLL_ACCESS: createRequestTypes(`${NAME}_POLL_ACCESS`),
  POLL_ACCESS_CONFIRM: createRequestTypes(`${NAME}_POLL_ACCESS_CONFIRM`),
  REDIRECT_TO: `${NAME}_REDIRECT_TO`,
  GRANT_ACCEPT_FLOW: `${NAME}_REDIRECT_TO`,
  SHOW_DOWNLOAD_APP: `${NAME}_SHOW_DOWNLOAD_APP`,
  SET_LOGIN_FLOW: `${NAME}_SET_LOGIN_FLOW`,
  SET_REGISTER_FLOW: `${NAME}_SET_REGISTER_FLOW`,
};

export const actions = {
  login: createRequestAction(types.LOGIN),
  loginFlow: createAction(types.LOGIN_FLOW),
  register: createRequestAction(types.REGISTER),
  checkApp: createRequestAction(types.CHECK_APP),
  accessInfo: createRequestAction(types.ACCESS_INFO),
  checkUsername: createRequestAction(types.CHECK_USERNAME),
  checkUid: createRequestAction(types.CHECK_UID),
  giveAccess: createRequestAction(types.GIVE_ACCESSES),
  grantAcceptFlow: createAction(types.GRANT_ACCEPT_FLOW),
  redirectTo: createAction(types.REDIRECT_TO),
  pollAccess: createRequestAction(types.POLL_ACCESS),
  pollAccessConfirm: createRequestAction(types.POLL_ACCESS_CONFIRM),
  showDownloadApp: createAction(types.SHOW_DOWNLOAD_APP),
  setLoginFlow: createAction(types.SET_LOGIN_FLOW),
  setRegisterFlow: createAction(types.SET_REGISTER_FLOW),
};

export const handlers = {
  ...createRequestHandlers(types.LOGIN, {
    successHandler(state, { payload }) {
      return {
        ...state,
        fetching: false,
        loginData: payload,
      };
    },
  }),
  ...createRequestHandlers(types.REGISTER, {
    successHandler(state, { payload }) {
      return {
        ...state,
        fetching: false,
        registerData: payload,
      };
    },
  }),
  ...createRequestHandlers(types.CHECK_UID, {
    successHandler(state, { payload }) {
      return {
        ...state,
        fetching: false,
        uidData: payload,
      };
    },
  }),
  ...createRequestHandlers(types.CHECK_USERNAME, {
    successHandler(state, { payload }) {
      return {
        ...state,
        fetching: false,
        usernameData: payload,
      };
    },
  }),
  ...createRequestHandlers(types.CHECK_APP, {
    successHandler(state, { payload }) {
      return {
        ...state,
        fetching: false,
        checkAppData: payload,
      };
    },
  }),
  ...createRequestHandlers(types.ACCESS_INFO, {
    successHandler(state, { payload }) {
      return {
        ...state,
        fetching: false,
        accessInfo: payload,
      };
    },
  }),
  ...createRequestHandlers(types.GIVE_ACCESSES, {
    successHandler(state, { payload }) {
      return {
        ...state,
        fetching: false,
        accessData: payload,
      };
    },
  }),
  ...createRequestHandlers(types.POLL_ACCESS, {
    successHandler(state, { payload }) {
      return {
        ...state,
        fetching: false,
        pollAccess: payload,
      };
    },
  }),
  ...createRequestHandlers(types.POLL_ACCESS_CONFIRM, {
    successHandler(state, { payload }) {
      return {
        ...state,
        fetching: false,
        pollAccessConfirm: payload,
      };
    },
  }),
  [types.SHOW_DOWNLOAD_APP]: (state, { payload }) => ({
    ...state,
    showDownloadApp: payload,
  }),
  [types.SET_LOGIN_FLOW]: state => ({
    ...state,
    isLoginFlow: true,
    isRegisterFlow: false,
  }),
  [types.SET_REGISTER_FLOW]: state => ({
    ...state,
    isLoginFlow: false,
    isRegisterFlow: true,
  }),
};

export default createReducer(initialState, handlers);
