import { takeLatest, put, select } from 'redux-saga/effects';
import { history } from 'modules/history';
import routes from 'constants/routes';
import { SHORT_FLOW, FULL_FLOW } from '../constants/flows';
import { DISEASE, HEALTHFUL } from '../constants/result';
import { NAME as feelHealthyName } from '../reducers/feelHealthy';
import { NAME as symptomsName } from '../reducers/symptoms';
import { NAME as chronicDiseasesName } from '../reducers/chronicDiseases';
import { NAME as coronaQuestionsName } from '../reducers/coronaQuestions';

import { types, actions } from '../reducers/tracker';

function* get() {
  try {
    const { chronicDiseases } = yield select(
      ({ [chronicDiseasesName]: { data: chronicDiseases } }) => ({
        chronicDiseases,
      })
    );
    const isChronicDiseasesSelected = Object.values(chronicDiseases).some(
      v => v
    );
    yield put(
      actions.setFlow(isChronicDiseasesSelected ? SHORT_FLOW : FULL_FLOW)
    );
    yield put(actions.get.success());
  } catch (e) {
    yield put(actions.setFlow(SHORT_FLOW));
    yield put(actions.get.failure());
  }
}

function* submit() {
  try {
    const {
      symptoms,
      feelHealthy,
      chronicDiseases,
      coronaQuestions,
    } = yield select(
      ({
        [symptomsName]: { data: symptoms },
        [feelHealthyName]: { data: feelHealthy },
        [chronicDiseasesName]: { data: chronicDiseases },
        [coronaQuestionsName]: { data: coronaQuestions },
      }) => ({
        symptoms,
        feelHealthy,
        chronicDiseases,
        coronaQuestions,
      })
    );
    console.warn({
      symptoms,
      feelHealthy,
      chronicDiseases,
      coronaQuestions,
    });
    yield put(actions.resetStep());
    history.push(routes.thank);
    yield put(actions.setResult(HEALTHFUL));
  } catch (e) {
    yield put(actions.resetStep());
    yield put(actions.setResult(DISEASE));
  }
}

export default function* sagas() {
  yield takeLatest(types.GET.REQUEST, get);
  yield takeLatest(types.SUBMIT, submit);
}
