import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';

import { translation as en } from './en';
import { translation as de } from './de';
import { translation as fr } from './fr';
import { getLanguage } from 'utils/ls';

const EN = 'en';
const FR = 'fr';
const DE = 'de';

export const ACCESSED_LANGUAGES = [EN, FR, DE];

const getDefaultLanguage = () => {
  const language = getLanguage();
  if (language && ACCESSED_LANGUAGES.includes(language)) {
    return language;
  }
  return EN;
};

const defaultLanguage = getDefaultLanguage();

i18n.use(initReactI18next).init({
  resources: {
    [EN]: {
      translation: en,
    },
    [DE]: {
      translation: de,
    },
    [FR]: {
      translation: fr,
    },
    fallbackLng: EN,
  },
  lng: defaultLanguage,
});

export { i18n };
