import { connect } from 'react-redux';
import { getAllParams } from 'utils/query';
import { NAME, actions } from 'modules/auth/reducers/auth';
import Grant from 'modules/auth/components/Grant/Grant';

export default connect(
  ({ [NAME]: { data, fetching, error }, router: { location } }) => {
    return {
      data,
      error,
      fetching,
      params: getAllParams(location),
    };
  },
  dispatch => ({
    grantAcceptFlow: () => dispatch(actions.grantAcceptFlow()),
    redirectTo: to => dispatch(actions.redirectTo(to)),
  })
)(Grant);
